import uniIcons from '../unis/uni-icons/uni-icons.vue'
import {mapState} from 'vuex'
export default{
	components:{
		uniIcons
	},
	props:{
	    tableList:Object,
		column:Object,
		tableId:[Number,String],
		columnId:[Number,String],
		tmpId:[String,Number],
		valueName:[String,Number],
		cellInfo:[Object],
		tabletype:[Number,String]
	},
	data(){
		return{
			focus:false,
			inputVal:'',
			classId:'wb'+this.tableId+this.columnId
		}
	},
	computed:{
		fixedHeight(){
			if(this.tabletype ==1 || this.tabletype ==2) return true;	
		},
		value(){
			return this.tableList[this.valueName];
		},
		showLongWen(){
			if(this.column.cLen){
				return true;
			}else if(this.value && this.value.length*30 > this.column.dd){
				return true;
			}
		},
		// showLongWen(){
		// 	if(this.column.cId ===2 && this.column.cLen && this.cLen >= 256){
		// 		return true;
		// 	}
		// },
		clipValue(){
			let value = this.value;
			if(value && value.length*30 > this.column.dd){
				let valueArr = value.split('').reverse();
				let newValue = [];
				let nwidth = 0;
				for(let i in valueArr){
					if(valueArr[i].charCodeAt()>255){
						nwidth += 30;		
					}else if(/^[0-9]+$/.test(valueArr[i])){			
						nwidth += 17.6;
					}else if(/^[a-z]+$/.test(valueArr[i])){			
						nwidth += 19;
					}else if(/^[A-Z]+$/.test(valueArr[i])){			
						nwidth += 21;
					}else{
						nwidth += 20;
					}
					if(nwidth>this.column.dd){
						break;
					}else{
						newValue.push(valueArr[i])
					}
				}
				// let maxLength = Math.floor(this.column.dd/30);
				// console.log(this.column.dd,value)
				value = newValue.reverse().join('')	
			}
			return value;
		},
		...mapState({
			recordsAll:state=>state.table.recordsAll,
			editable:state=>state.table.editable,
			activeItem:state=>state.table.activeItem,
			selectData:state=>state.table.deptData,
			color:state=>state.color
		}),
		changeValue(){	
			for(let item of this.recordsAll){
				if(	item
					&& item.tmpTableId == this.cellInfo.tmpTableId
					&& item.columnId == this.columnId 
					&& item.tableId == this.tableId 
				){
					// console.log(item.value)
					return item.value
				}
			}
		},
		isActive(){
			if( this.activeItem
				&& this.activeItem.tmpTableId == this.cellInfo.tmpTableId
				&& this.activeItem.columnId == this.columnId 
				&& this.activeItem.tableId == this.tableId 
			){
				return true;
			}						
		},
		//控制弹出卡片
		showCard(){
			if(this.isActive && this.activeItem.showCard){
				return this.activeItem.showCard;
			}
		},
		showInput(){
			if(this.isActive && this.activeItem.showInput){
				return this.activeItem.showInput;
			}
		},
		nowValue(){
			let nowValue=''
			if(this.changeValue){
				return this.inputVal;
			}else{
				return this.value;
			}
		}
	},
	methods:{
		toEdit(){
			if(this.showLongWen){
				this.toShowCard()
			}else{
				this.onFocus()
			}
		},
		async toShowCard(){	
			if(this.showCard){
				this.hideShowCard();
				return;
			}
			this.inputVal = this.nowValue;
						
		    //出现弹出层 保存筛选数据
			this.$store.dispatch("changeActiveItem",{
				cellInfo:this.cellInfo,
				cellparams:{
					showCard:true,
					classId:this.classId
				}
			})
		},		
		//点击展开选项
		onFocus(){	
			// console.log("focus")
			this.focus = true;
			this.inputVal = this.nowValue;
			this.$store.dispatch("changeActiveItem",{
				cellInfo:this.cellInfo
			})
		},
		// 选择选中部门
		//选中盒子  选中样式 选中数据 不考虑 父级与子级
		onBlur(e){
			this.confirmFun(e.value)
		},
		//确认选择
		confirmFun(value){  
			// console.log("confirm",this.inputVal,value)
			this.$store.dispatch("changeActiveItem",{
				cellInfo:this.cellInfo,
				cellparams:{
					value:this.inputVal,
					isupdate:true,
					showCard:false
				}
			})						
			this.hideShowCard();							
		},
		//取消选择
		cancelFun(){		
			this.hideShowCard()
		},
		hideShowCard(){
			this.$store.dispatch("changeActiveItem",{
				cellInfo:this.cellInfo,
				cellparams:{showCard:false}
			})
			this.focus = false;
			this.$store.commit('initActiveItem');
						// console.log(this.isActive,this.changeValue)
		}
	}
}
